<template>
  <v-navigation-drawer app id="nav" :value="drawer" fixed>
    <v-toolbar color="transparent">
      <div>
        <logo :size="0.5"/>
      </div>
    </v-toolbar>
    <v-list dense class="navigation_list">
      <router-link to="/cashvue/">
        <v-list-tile class="navigation__elem">
          <v-list-tile-action>
            <v-icon>fas fa-home</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              Start
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </router-link>
      <v-list-group v-if="authenticated">
        <v-list-tile class="navigation__elem pl-1" slot="activator">
          <v-list-tile-action>
            <v-icon>fas fa-wallet</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              Wallet
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <router-link to="/cashvue/dashboard">
          <v-list-tile class="navigation__elem pl-4">
            <v-list-tile-action>
              <v-icon>fas fa-wallet</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>
                Dashboard
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </router-link>
        <router-link to="/cashvue/expenses">
          <v-list-tile class="navigation__elem pl-4">
            <v-list-tile-action>
              <v-icon>fas fa-wallet</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>
                Expenses
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </router-link>
        <router-link to="/cashvue/earnings">
          <v-list-tile class="navigation__elem pl-4">
            <v-list-tile-action>
              <v-icon>fas fa-wallet</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>
                Earnings
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </router-link>
      </v-list-group>
      <router-link to="/cashvue/profile">
        <v-list-tile class="navigation__elem" v-if="authenticated">
          <v-list-tile-action>
            <v-icon>fas fa-users-cog</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              Profile
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </router-link>
      <a href="#"
        @click="login()"
        v-if="!authenticated">
        <v-list-tile class="navigation__elem log in">
          <v-list-tile-action>
            <v-icon>fas fa-sign-in-alt</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              Log in / Sign up
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </a>
      <a href="#"
        @click="logout()"
        v-if="authenticated">
        <v-list-tile class="navigation__elem log out">
          <v-list-tile-action>
            <v-icon>fas fa-sign-out-alt</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              Log out
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </a>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import { mapGetters } from 'vuex';
import Logo from './Logo';

export default {
  name: 'Navigation',
  components: {
    Logo,
  },
  props: ['authenticated'],
  computed: {
    login() {
      this.$emit('login');
    },
    logout() {
      this.$emit('logout');
    },
    ...mapGetters({
      drawer: 'getDrawer',
    }),
  },
};
</script>



